@use "../partials/" as vars;
@use "../mixins/" as mix;

html {
	scroll-behavior: smooth;
	scrollbar-color: vars.$clr-pill vars.$clr-text;
}

body {
	width: 100%;
	height: 100%;
	position: relative;

	background-color: vars.$clr-body;
}

.page-container {
	width: 95%;
	position: relative;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
	align-content: center;
margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-areas:
		"nav nav nav nav "
		"header header header header"
		"main main main main"
		"footer footer footer footer";

	@include mix.media(1200px) {
		grid-template-columns: repeat(3, 1fr);
		grid-template-areas:
			"nav nav nav "
			"header header header"
			"main main main"
			"footer footer footer";
	}
	@include mix.media(900px) {
		grid-template-columns: repeat(2, 1fr);
		grid-template-areas:
			"nav nav "
			"header header"
			"main main"
			"footer footer";
	}

	@include mix.media(800px) {
		grid-template-columns: repeat(1, 1fr);
		grid-template-areas:
			"nav"
			"header"
			"main"
			"footer";
	}
}
